"use client";

import { useState, type FormEvent, useRef, ChangeEvent } from 'react';
import Image from 'next/image';
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { UploadCloud, Sparkles, ImageOff } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

interface PlantAnalysisFormProps {
  onSubmit: (photoDataUri: string, description: string, originalImageName: string) => void;
  isAnalyzing: boolean;
}

export default function PlantAnalysisForm({ onSubmit, isAnalyzing }: PlantAnalysisFormProps) {
  const [description, setDescription] = useState('');
  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const [imageFile, setImageFile] = useState<File | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const { toast } = useToast();

  const handleImageChange = (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) { // 5MB limit
        toast({
          variant: "destructive",
          title: "Image too large",
          description: "Please upload an image smaller than 5MB.",
        });
        if (fileInputRef.current) {
            fileInputRef.current.value = ""; // Reset file input
        }
        setImageFile(null);
        setImagePreview(null);
        return;
      }
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    } else {
      setImageFile(null);
      setImagePreview(null);
    }
  };

  const handleSubmit = async (event: FormEvent) => {
    event.preventDefault();
    if (!imageFile || !imagePreview) {
      toast({
        variant: "destructive",
        title: "No image selected",
        description: "Please upload an image of your plant.",
      });
      return;
    }
    onSubmit(imagePreview, description, imageFile.name);
  };

  return (
    <Card className="w-full max-w-lg mx-auto shadow-lg">
      <CardHeader>
        <CardTitle className="text-2xl flex items-center gap-2">
          <Sparkles className="text-primary h-6 w-6" />
          Analyze Your Plant
        </CardTitle>
        <CardDescription>
          Upload an image and add a description to detect potential plant diseases.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-2">
            <Label htmlFor="plant-image" className="text-base">Plant Image</Label>
            <Input
              id="plant-image"
              type="file"
              accept="image/png, image/jpeg, image/webp"
              onChange={handleImageChange}
              ref={fileInputRef}
              className="file:text-primary file:font-semibold hover:file:bg-primary/10"
              aria-describedby="plant-image-hint"
            />
            <p id="plant-image-hint" className="text-sm text-muted-foreground">
              PNG, JPG, WEBP. Max 5MB.
            </p>
          </div>

          {imagePreview && (
            <div className="mt-4 border border-border rounded-md p-2 bg-muted/30">
              <Image
                src={imagePreview}
                alt="Plant preview"
                width={400}
                height={300}
                className="rounded-md object-contain max-h-[300px] w-full"
                data-ai-hint="plant agriculture"
              />
            </div>
          )}
          {!imagePreview && (
             <div className="mt-4 border border-dashed border-border rounded-md p-8 flex flex-col items-center justify-center bg-muted/30 h-[200px]">
                <ImageOff className="h-16 w-16 text-muted-foreground mb-2" />
                <p className="text-muted-foreground">Image preview will appear here</p>
             </div>
          )}


          <div className="space-y-2">
            <Label htmlFor="plant-description" className="text-base">Description (Optional)</Label>
            <Textarea
              id="plant-description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="e.g., Yellow spots on leaves, wilting stems, etc."
              rows={3}
              className="resize-none"
            />
          </div>

          <Button type="submit" disabled={isAnalyzing || !imageFile} className="w-full text-base py-3">
            {isAnalyzing ? (
              <>
                <UploadCloud className="mr-2 h-5 w-5 animate-pulse" />
                Analyzing...
              </>
            ) : (
              <>
                <Sparkles className="mr-2 h-5 w-5" />
                Analyze Plant
              </>
            )}
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}
