"use client";

import { useState, useEffect, useCallback } from 'react';
import type { HistoryEntry } from '@/types';

const HISTORY_STORAGE_KEY = 'verdantViewHistory';

export function useHistory() {
  const [history, setHistory] = useState<HistoryEntry[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    try {
      const storedHistory = localStorage.getItem(HISTORY_STORAGE_KEY);
      if (storedHistory) {
        setHistory(JSON.parse(storedHistory));
      }
    } catch (error) {
      console.error("Failed to load history from localStorage:", error);
      // Optionally, clear corrupted data or handle error
      // localStorage.removeItem(HISTORY_STORAGE_KEY);
    } finally {
      setIsLoading(false);
    }
  }, []);

  const addHistoryEntry = useCallback((entry: Omit<HistoryEntry, 'id' | 'date'>) => {
    setHistory(prevHistory => {
      const newEntry: HistoryEntry = {
        ...entry,
        id: Date.now().toString(), // Simple unique ID
        date: new Date().toISOString(),
      };
      const updatedHistory = [newEntry, ...prevHistory].slice(0, 50); // Limit history size
      try {
        localStorage.setItem(HISTORY_STORAGE_KEY, JSON.stringify(updatedHistory));
      } catch (error) {
        console.error("Failed to save history to localStorage:", error);
      }
      return updatedHistory;
    });
  }, []);

  const clearHistory = useCallback(() => {
    setHistory([]);
    try {
      localStorage.removeItem(HISTORY_STORAGE_KEY);
    } catch (error) {
      console.error("Failed to clear history from localStorage:", error);
    }
  }, []);
  
  const getHistoryEntryById = useCallback((id: string): HistoryEntry | undefined => {
    return history.find(entry => entry.id === id);
  }, [history]);


  return { history, addHistoryEntry, clearHistory, isLoading, getHistoryEntryById };
}
