"use client";

import { useState } from 'react';
import PlantAnalysisForm from '@/components/PlantAnalysisForm';
import AnalysisResult from '@/components/AnalysisResult';
import LoadingSpinner from '@/components/LoadingSpinner';
import { useToast } from '@/hooks/use-toast';
import { useHistory } from '@/hooks/useHistory';
import { analyzePlantDisease } from '@/ai/flows/analyze-plant-disease';
import type { AnalyzePlantDiseaseOutput } from '@/ai/flows/analyze-plant-disease';
import { reasonAboutPlantSuggestions } from '@/ai/flows/reason-about-plant-suggestions';
import type { AnalysisResultData } from '@/types';
import { ALL_PLANT_SUGGESTIONS } from '@/lib/constants';
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Terminal } from "lucide-react";


export default function HomePage() {
  const [analysisResult, setAnalysisResult] = useState<AnalysisResultData | null>(null);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();
  const { addHistoryEntry } = useHistory();

  const handleAnalysisSubmit = async (photoDataUri: string, description: string, originalImageName: string) => {
    setIsAnalyzing(true);
    setError(null);
    setAnalysisResult(null);

    try {
      const diseaseAnalysis: AnalyzePlantDiseaseOutput = await analyzePlantDisease({
        photoDataUri,
        description,
      });

      let finalSuggestions = diseaseAnalysis.suggestedSolutions || [];

      if (diseaseAnalysis.diseaseDetected && diseaseAnalysis.diseaseName) {
         // Use a broader set of suggestions if the AI didn't provide any for a detected disease.
        const suggestionsToFilter = (diseaseAnalysis.suggestedSolutions && diseaseAnalysis.suggestedSolutions.length > 0) 
                                    ? diseaseAnalysis.suggestedSolutions
                                    : ALL_PLANT_SUGGESTIONS;
        
        const filteredSuggestions = await reasonAboutPlantSuggestions({
          plantCondition: diseaseAnalysis.diseaseName,
          allSuggestions: suggestionsToFilter,
        });
        finalSuggestions = filteredSuggestions.length > 0 ? filteredSuggestions : (diseaseAnalysis.suggestedSolutions || []);
      } else if (!diseaseAnalysis.diseaseDetected) {
        // If no disease detected, we might still want to offer general advice based on description
        // For now, if no disease, no specific suggestions unless AI provided some.
        // Or, we could use reasonAboutPlantSuggestions with a generic "healthy plant" condition
        // For simplicity, we'll use AI's suggestions or empty if none.
        finalSuggestions = diseaseAnalysis.suggestedSolutions || [];
      }


      const resultData: AnalysisResultData = {
        diseaseDetected: diseaseAnalysis.diseaseDetected,
        diseaseName: diseaseAnalysis.diseaseName || (diseaseAnalysis.diseaseDetected ? "Unknown Issue" : "Healthy"),
        suggestedSolutions: finalSuggestions,
      };
      
      setAnalysisResult(resultData);
      addHistoryEntry({
        imageDataUri: photoDataUri,
        originalImageName,
        description,
        ...resultData,
      });

      toast({
        title: "Analysis Complete",
        description: diseaseAnalysis.diseaseDetected 
          ? `Detected: ${diseaseAnalysis.diseaseName || "Unknown Issue"}`
          : "Plant appears healthy!",
      });

    } catch (e: any) {
      console.error("Analysis failed:", e);
      const errorMessage = e.message || "An unknown error occurred during analysis.";
      setError(errorMessage);
      toast({
        variant: "destructive",
        title: "Analysis Failed",
        description: errorMessage,
      });
    } finally {
      setIsAnalyzing(false);
    }
  };

  return (
    <div className="flex flex-col items-center space-y-8">
      <PlantAnalysisForm onSubmit={handleAnalysisSubmit} isAnalyzing={isAnalyzing} />
      
      {isAnalyzing && (
        <div className="flex flex-col items-center space-y-2 p-4">
          <LoadingSpinner size={48} />
          <p className="text-lg text-primary animate-pulse">Analyzing your plant, please wait...</p>
        </div>
      )}

      {error && (
         <Alert variant="destructive" className="w-full max-w-lg mx-auto">
           <Terminal className="h-4 w-4" />
           <AlertTitle>Error</AlertTitle>
           <AlertDescription>
             {error}
           </AlertDescription>
         </Alert>
      )}

      {analysisResult && !isAnalyzing && <AnalysisResult result={analysisResult} />}
    </div>
  );
}
