"use client";

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { CheckCircle2, AlertTriangle, ListChecks, Leaf } from 'lucide-react';
import type { AnalysisResultData } from '@/types';

interface AnalysisResultProps {
  result: AnalysisResultData | null;
}

export default function AnalysisResult({ result }: AnalysisResultProps) {
  if (!result) return null;

  return (
    <Card className="w-full max-w-lg mx-auto mt-8 shadow-lg">
      <CardHeader>
        <CardTitle className="text-2xl flex items-center gap-2">
          <Leaf className="text-primary h-6 w-6" />
          Analysis Results
        </CardTitle>
        <CardDescription>
          Here's what our AI found about your plant.
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        {result.diseaseDetected ? (
          <Alert variant="destructive">
            <AlertTriangle className="h-5 w-5" />
            <AlertTitle className="font-semibold">Disease Detected: {result.diseaseName || "Unknown Disease"}</AlertTitle>
            <AlertDescription>
              Your plant may be affected. See suggestions below.
            </AlertDescription>
          </Alert>
        ) : (
          <Alert variant="default" className="bg-primary/10 border-primary/30">
            <CheckCircle2 className="h-5 w-5 text-primary" />
            <AlertTitle className="font-semibold text-primary">No Disease Detected</AlertTitle>
            <AlertDescription>
              Your plant appears to be healthy based on the analysis.
            </AlertDescription>
          </Alert>
        )}

        {result.suggestedSolutions && result.suggestedSolutions.length > 0 && (
          <div>
            <h3 className="text-lg font-semibold mb-2 flex items-center gap-2">
              <ListChecks className="text-accent h-5 w-5" />
              Suggested Actions:
            </h3>
            <ul className="list-disc list-inside space-y-1 pl-2 text-foreground/90 bg-muted/30 p-3 rounded-md">
              {result.suggestedSolutions.map((solution, index) => (
                <li key={index}>{solution}</li>
              ))}
            </ul>
          </div>
        )}

        {!result.diseaseDetected && (!result.suggestedSolutions || result.suggestedSolutions.length === 0) && (
           <p className="text-sm text-muted-foreground">Keep up the good work with your plant care!</p>
        )}
         {result.diseaseDetected && (!result.suggestedSolutions || result.suggestedSolutions.length === 0) && (
           <p className="text-sm text-muted-foreground">No specific solutions were suggested for the detected issue. Consider general plant care or consulting a local expert.</p>
        )}
      </CardContent>
    </Card>
  );
}
