// 'use server';

/**
 * @fileOverview This flow filters plant suggestions based on the plant's condition.
 *
 * - reasonAboutPlantSuggestions - A function that filters plant suggestions based on the plant's condition.
 * - ReasonAboutPlantSuggestionsInput - The input type for the reasonAboutPlantSuggestions function.
 * - ReasonAboutPlantSuggestionsOutput - The return type for the reasonAboutPlantSuggestions function.
 */

'use server';

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const ReasonAboutPlantSuggestionsInputSchema = z.object({
  plantCondition: z.string().describe('The condition of the plant.'),
  allSuggestions: z.array(z.string()).describe('All possible suggestions for plant care.'),
});
export type ReasonAboutPlantSuggestionsInput = z.infer<typeof ReasonAboutPlantSuggestionsInputSchema>;

const ReasonAboutPlantSuggestionsOutputSchema = z.array(z.string()).describe('The filtered suggestions relevant to the plant condition.');
export type ReasonAboutPlantSuggestionsOutput = z.infer<typeof ReasonAboutPlantSuggestionsOutputSchema>;

export async function reasonAboutPlantSuggestions(input: ReasonAboutPlantSuggestionsInput): Promise<ReasonAboutPlantSuggestionsOutput> {
  return reasonAboutPlantSuggestionsFlow(input);
}

const suggestionFilterPrompt = ai.definePrompt({
  name: 'suggestionFilterPrompt',
  input: {schema: ReasonAboutPlantSuggestionsInputSchema},
  output: {schema: ReasonAboutPlantSuggestionsOutputSchema},
  prompt: `Given the following plant condition: {{{plantCondition}}}, and a list of suggestions: {{{allSuggestions}}}, filter the suggestions to only include those that are relevant to the plant's condition. Return the filtered list of suggestions.

  Instructions:  Do not return any suggestions that are not relevant to the plant's condition. Only respond with the suggestions, each on their own line. Do not include any other text. If no suggestions are relevant, return an empty array.`, 
});

const reasonAboutPlantSuggestionsFlow = ai.defineFlow(
  {
    name: 'reasonAboutPlantSuggestionsFlow',
    inputSchema: ReasonAboutPlantSuggestionsInputSchema,
    outputSchema: ReasonAboutPlantSuggestionsOutputSchema,
  },
  async input => {
    const {output} = await suggestionFilterPrompt(input);
    return output!;
  }
);
